*{
  padding: 0;
  margin: 0;
}
ul,li{
  list-style: none;
}
body{
  background-color: #ddd;
}
/* header */
.headerwrap{
  height: 50px;
  background-color: #444;
}
.headerwrap header{
  width: 1200px;
  margin: 0 auto;
}
.headerwrap img{
  margin-top: 10px;
  width: 120px;
}
.headerwrap a{
  margin: 0 20px;
  color: #fff;
}
/* main */
.main{
  width: 1200px;
  margin: 30px auto;
}
.content{
  width: 900px;
  float: left;
  background-color: white;
}
.sider{
  width: 270px;
  height: 500px;
  float: right;
}
.inner{
  background-color: #fff;
  padding:10px;
  margin-bottom: 10px;
  border-radius: 3px;
}
p{
  font-size: 14px;
  word-break: break-word;
  margin:0 0 10px;
}
.span-infor{
    display: inline-block;
    float: none;
    padding: 3px 10px;
    border: none;
    margin: 0;
    font-size: 14px;
    transition: all .2s ease-in-out;
    cursor: pointer;
    letter-spacing: 2px;
    box-shadow: none;
    border-radius: 3px;
    line-height: 2em;
    vertical-align: middle;
    color: #fff;
    background-color: #5bc0de;
}
img{
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border:0;
  margin-bottom: 10px;
}
a{
  font-size: 14px;
  color:#778087;
}
.spantop{
  font-size: 13px;
  color: #778087;
}
li{
  margin-bottom: 8px;
}
