.boxli{
  margin: 20px;
  border-top:1px solid #f6f6f6;
  border-radius:3px;
}
.title{
  background-color: #f6f6f6;
  font-size: 14px;
}
.boxp{
  float: left;
  margin-left: 5px;
  font-size: 12px;
}
.bottom{
  background-color: #fff;
  width: 100%;
  float: left;
  margin-top: 20px;
  bottom: 0;
}
.bottomspan{
  width: 100%;
  float: left;
  margin-left: 160px;
  margin-top:20px;
  font-Size:14px;
  color: #778087;
}
.signin{
  margin:auto;
}
/*分页*/
.pages{
  border:1px solid #778087;
  width:30px;
  height: 30px;
  text-align: center;
  background-color: #fff;
}